<template>
    <div id="table">
        <b-table striped hover :items="items" :fields="fields">
            <template #cell(Actions)="row">
                <div class="wider">
                    <b-button variant="danger" size="sm" @click="info(row.item, row.index, $event.target)" class="mr-1">Ban</b-button>
                    <b-button variant="success" class="m-1">Unban</b-button>
                </div>
            </template>
        </b-table>
        <b-modal :id="infoModal.id" :title="infoModal.title" hide-footer>
            <div class="d-block text-center">
                Are you sure?
            </div>
            <div class="d-flex justify-content-center">
                <b-button class="mt-3 m-5 w-25" block variant="success" @click="ban">Yes</b-button>
                <b-button class="mt-3 m-5 w-25" block variant="danger" @click="this.$bvModal.hide(infoModal.id)">No</b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
    import AuthService from '@/services/authService.js'        
    export default {
        data() {
            return {
                keyword: '',
                dataArray: null,
                fields: [
                    { key: 'firstName', label: 'First name', sortable: true },
                    { key: 'thirdName', label: 'Last name', sortable: true },
                    { key: 'email', label: 'Email', sortable: true },
                    { key: 'phone', label: 'Phone', sortable: true },
                    { key: 'Actions' }
                ],
                infoModal: {
                    id: 'info-modal',
                    title: '',
                    content: ''
                }
            }
        },
        async mounted() {
            this.dataArray = await AuthService.getUsers();
        },
        computed: {
            items() {
                if (this.dataArray == undefined) return;
                return this.dataArray;
            }
        },
        methods: {
            info(item, index, button) {
                this.infoModal.title = `You are about to ban user ${item.username}`
                this.$root.$emit('bv::show::modal', this.infoModal.id, button)
            }
        }
    }
</script>

<style scoped>
    .wider {
        width:200px;
    }
</style>